import React from "react";
import ReCAPTCHA from "react-google-recaptcha";

const SITE_KEY = process.env.REACT_APP_RECAPTCHA_SITE_KEY || ''

const ReCaptchaComponent = ({onVerify}) => {
    return (
        <div className="mt=10">
            <ReCAPTCHA
                sitekey={SITE_KEY}
                onChange={onVerify}
            />
        </div>
    );
};

export default ReCaptchaComponent;